import { Injectable } from '@angular/core';
import { Validators, FormGroup, ValidatorFn, ValidationErrors, FormArray } from '@angular/forms';
import * as date from 'date-and-time';
import * as moment from 'moment';

@Injectable({
  providedIn: 'root'
})
export class ValidationRulesService {

  constructor() {}

  validationForEmail(): Validators {
    return Validators.pattern(/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,4})+$/);
  }

  validationForNullValue(): Validators {
    return Validators.nullValidator;
  }

  validationForOnlyNumbers(): Validators {
    return Validators.pattern('^([1234567890])*$');
  }

  validationForRequired(): Validators {
    return Validators.required;
  }

  validationForRequiredTrue(): Validators {
    return Validators.requiredTrue;
  }

  validationForMaxNumber(max: number): Validators {
    return Validators.max(max);
  }

  validationForMinNumber(min: number): Validators {
    return Validators.min(min);
  }

  validationForMaxLength(max: number): Validators {
    return Validators.maxLength(max);
  }

  validationWithCallBack( key: string, done: (value: FormGroup) => boolean): Validators {
      return (control: FormGroup): ValidationErrors | null => {
        const obj = {};
        obj[key] = true;
        if ( !control.parent) {
          return obj;
        }
        return done(control) ? null : obj ;
      };
  }

  phoneNumber3FieldsValidation( key: string): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[key] = true;
      if (  control.get('actividadLaboral').value === 'A' ||
            control.get('actividadLaboral').value === 'P' ||
            control.get('actividadLaboral').value === 'T' ) {
        return null;
      }
      if ( control.get('indicativo').value === '' && control.get('telefono').value === '' && control.get('extencion').value === '' ) {
        return null;
      }
      if ( control.get('indicativo').value !== '' && control.get('telefono').value !== '') {
        return null;
      }
      return obj ;
    };
}

  validationWithArrayCallBack( key: string, done: Array<(value: FormGroup) => boolean>): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      const obj = {};
      obj[key] = true;
      if ( !control.parent) {
        return obj;
      }
      const results = done.map(
        item => item(control)
      );
      return results.reduce((itemA, itemB) => itemA && itemB, true) ? null : obj ;
    };
}

  validationForMinLength(min: number): Validators {
    return Validators.minLength(min);
  }

  validationForPattern(pattern: string | RegExp): Validators {
    return Validators.pattern(pattern);
  }

  validationClaveVirtual(field1: string, field3: string): ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const str = control.get(field1).value;
      const n = str.search(
      /(0123|1234|2345|3456|4567|5678|6789|7890|3210|4321|5432|6543|7654|8765|9876|0987|0000|1111|2222|3333|4444|5555|6666|7777|8888|9999)/i
        );
      return (n === -1)  ? null : obj;
    };
  }

  validationForValue(field1: string, value1: string, field2: string, field3: string): ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const fieldUno = control.get(field1).value;
      if ( fieldUno === value1 ) {
        const fieldDos = control.get(field2).value;
        if ( fieldDos === '') {
          return obj;
        }
        return null;
      }
      return null;
    };
  }

  // validationForCelPhone(): Validators {
  //   return (control: FormGroup): ValidationErrors | null => {
  //     const str = control.value;
  //     if ( !str ) {
  //       return { celPhone: true };
  //     }
  //     const s = str.match(`(${this.prefijos})(2|3|4|5|6|7|8|9)[0-9]{6}`);
  //     const u = str.match(`[0-9]{3}(8888888|7777777|6666666|5555555|4444444|3333333|2222222|9999999)`);
  //     if ( s || u)  {
  //         return null;
  //     }
  //     return { celPhone: true };
  //   };
  // }

  validationForSelect(valores: () => Array<any>, required: (control: FormGroup) => boolean = (control: FormGroup) => true): Validators {
    return (control: FormGroup): ValidationErrors | null => {
        if ( required(control) )  {
          return null;
        }
        return valores().filter( item => {
          if ( item.value === control.value) {
            return true;
          }
          return false;
        }).length >= 1 ? null : { select: true };
    };
  }

  validationForSelectMultiple(
    valores: () => Array<any>,
    required: (control: FormGroup) => boolean = (control: FormGroup) => true
  ): Validators {
    return (control: FormGroup): ValidationErrors | null => {
        if ( required(control) )  {
          return null;
        }
        const va = valores().filter( item => {
          if ( (control.value as Array<string>).filter( data => item.value === data ).length !== 0 ) {
            return true;
          }
          return false;
        });
        return va.length !== 0 ? null : { select: true };
    };
  }


  validationForMinDate(fecha: string|number, type?: 'year' | 'month' | 'day'): Validators {
    return (control: FormGroup): ValidationErrors | null => {
        if ( !control.parent) {
          return { minDate: true };
        }
        const myDate = new Date(control.value);
        if ( typeof(fecha) === 'number') {
          if (type === 'year') {
            if ( moment(date.addYears(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
          if (type === 'month') {
            if ( moment(date.addMonths(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
          if (type === 'day') {
            if ( moment(date.addDays(new Date(), fecha)).isAfter(myDate) ) {
              return { minDate: true };
            }
          }
        }
        if ( typeof(fecha) === 'string') {
          if ( moment(new Date(fecha)).isAfter(myDate) ) {
            return { minDate: true };
          }
        }
        return null;
      };
  }

  validationForMaxDate(fecha: string|number, type: 'year' | 'month' | 'day' = 'day'): Validators {
    return (control: FormGroup): ValidationErrors | null => {
      if ( !control.parent) {
        return { maxDate: true };
      }
      const myDate = new Date(control.value);
      if ( typeof(fecha) === 'number') {
        if (type === 'year') {
          if ( moment(date.addYears(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
        if (type === 'month') {
          if ( moment(date.addMonths(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
        if (type === 'day') {
          if ( moment(date.addDays(new Date(), fecha)).isBefore(myDate) ) {
            return { maxDate: true };
          }
        }
      }
      if ( typeof(fecha) === 'string') {
        if ( moment(new Date(fecha)).isBefore(myDate) ) {
          return { maxDate: true };
        }
      }
      return null;
    };
  }

  validationContentInArray(field1: string, map: (pais: string) => boolean, field3: string): ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const str = control.get(field1).value;
      if ( map(str) ) {
        return null;
      }
      return obj;
    };
  }

  validationContentInArrayWithValue(field1: string, field2: string, value: string,
// tslint:disable-next-line: align
    map: ( pais: string ) => boolean, field3: string): ValidatorFn {
    const obj = {};
    obj[field3] = true;
    return (control: FormGroup): ValidationErrors | null => {
      const campo2 = control.get(field2).value;
      if (campo2 !== value) {
        return null;
      }
      const str = control.get(field1).value;
      if ( map(str) ) {
        return null;
      }
      return obj;
    };
  }

  get errorTypeEmail(): string {
    return 'pattern';
  }

  get errorTypeNullValue(): string {
    return 'null';
  }

  get errorTypeOnlyNumbers(): string {
    return 'pattern';
  }

  get errorTypeRequiredTrue(): string {
    return 'required';
  }

  get errorTypeRequired(): string {
    return 'required';
  }

  get errorTypeMaxNumber(): string {
    return 'max';
  }

  get errorTypeMinNumber(): string {
    return 'min';
  }

  get errorTypeMaxLength(): string {
    return 'maxlength';
  }

  get errorTypeMaxDate(): string {
    return 'maxDate';
  }

  get errorTypeMinDate(): string {
    return 'minDate';
  }

  get errorTypeCelPhone(): string {
    return 'celPhone';
  }

  get errorTypeMinLength(): string {
    return 'minlength';
  }

  get errorTypePattern(): string {
    return 'pattern';
  }

  get errorTypeSelect(): string {
    return 'select';
  }

  resetMyCallbackValidation(fields: Array<string>): Validators | ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      if ( !control.parent) {
        return null;
      }
      fields.forEach( item => {
        control.parent.get(item).setValue(control.parent.get(item).value);
      });
      return null;
    };
  }

  resetMyCallbackValidationArray(campo: string, fields: Array<string>): Validators | ValidatorFn {
    return (control: FormGroup): ValidationErrors | null => {
      if ( !control.parent) {
        return null;
      }
      (control.parent.get(campo) as FormArray).controls.forEach(
        itemArr => {
          fields.map( item => {

            itemArr.get(item).setValue(itemArr.get(item).value);
          });
        }
      );
      return null;
    };
  }
}
