import { Component, OnInit } from '@angular/core';
import { SendInformationService } from '../../service/SendInformation/send-information.service';
import { Observable } from 'rxjs';
import { LOADING } from '../../CONST';

@Component({
  selector: 'app-loader',
  templateUrl: './loader.component.html',
  styleUrls: ['./loader.component.scss', '../../../../../assets/cam/scss/components/common/_loader.scss']
})
export class LoaderComponent {

  mostrar: boolean;

  constructor(
    private loadingModalService: SendInformationService<boolean>,
  ) {
    this.loadingModalService.getData(LOADING).subscribe( this.response());
  }

  response(): (response: boolean) => void {
    return ( response: boolean) => {
      this.mostrar = response;
    };
  }

}
